  /* DEMO-SPECIFIC STYLES */
  .typewriter{
	color: #fff;
	font-family: monospace;
	overflow: hidden; /* Ensures the content is not revealed until the animation */
	border-right: .15em solid orange; /* The typwriter cursor */
	white-space: nowrap; /* Keeps the content on a single line */
	margin: 0 auto; /* Gives that scrolling effect as the typing happens */
	letter-spacing: .15em; /* Adjust as needed */
	-webkit-animation: 
	  typing 3.5s steps(30, end),
	  blink-caret .5s step-end infinite;
	        animation: 
	  typing 3.5s steps(30, end),
	  blink-caret .5s step-end infinite;
  }
  
  /* The typing effect */
  @-webkit-keyframes typing {
	from { width: 0 }
	to { width: 100% }
  }
  @keyframes typing {
	from { width: 0 }
	to { width: 100% }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
	from, to { border-color: transparent }
	50% { border-color: orange }
  }
  @keyframes blink-caret {
	from, to { border-color: transparent }
	50% { border-color: orange }
  }
:root {
	--size: 200px;
}

.skull {
	width: 200px;
	width: var(--size);
	height: 200px;
	height: var(--size);
	position: relative;
}

.skull .head {
	width: 100%;
	height: 75%;
	background-color: #fff;
	border-radius: 15% / 20%;
	position: absolute;
	top: 0;
	left: 0;
}

.skull .head:before,
.skull .head:after {
	content: "";
	position: absolute;
	border-radius: 50%;
	background-color: #000;
	width: 20%;
	height: 30%;
	bottom: 10%;
}

.skull .head:before {
	left: 10%;
}

.skull .head:after {
	right: 10%;
}

.skull .head .crack {
	width: 10%;
	height: 10%;
	background-color: #000;
	position: absolute;
	top: 0;
	right: 25%;
	-webkit-transform: skew(-15deg);
	        transform: skew(-15deg);
}

.skull .head .crack:before {
	content: "";
	position: absolute;
	top: 100%;
	left: calc(200px / 15);
	left: calc(var(--size) / 15);
	border-top: calc(200px / 8) solid #000;
	border-top: calc(var(--size) / 8) solid #000;
	border-right: calc(200px / 20) solid transparent;
	border-right: calc(var(--size) / 20) solid transparent;
	border-left: calc(200px / 40) solid transparent;
	border-left: calc(var(--size) / 40) solid transparent;
}

.skull .mouth {
	width: 40%;
	height: 25%;
	position: absolute;
	top: 75%;
	left: 30%;
	background-color: #fff;
	border-radius: 0 0 calc(200px / 10) calc(200px / 10);
	border-radius: 0 0 calc(var(--size) / 10) calc(var(--size) / 10);
}

.skull .mouth:before {
	content: "";
	position: absolute;
	width: 15%;
	height: 50%;
	border-radius: 50% / 30%;
	background-color: #000;
	left: 42.5%;
	top: -25%;
}

.container{
	  /* Center child horizontally*/
	  display: flex;
	  justify-content: center;
}

.skull .mouth .teeth {
	position: absolute;
	bottom: 0;
	left: 45%;
	width: 10%;
	height: 50%;
	margin-bottom: -5%;
	background-color: #000;
	border-radius: 50% / 20%;
}

.skull .mouth .teeth:before,
.skull .mouth .teeth:after {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: 50% / 20%;
	background-color: #000;
}

.skull .mouth .teeth:before {
	left: -250%;
}

.skull .mouth .teeth:after {
	right: -250%;
}
* {
	border: 0;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
:root {
	--hue: 223;
	--bg: hsl(var(--hue),10%,90%);
	--fg: hsl(var(--hue),10%,10%);
	--primary: hsl(var(--hue),90%,55%);
	--primary-l: hsl(var(--hue),90%,65%);
	--primary-d: hsl(var(--hue),90%,45%);
	--white: hsl(var(--hue),10%,100%);
	--white-d: hsl(var(--hue),10%,45%);
}
body {
	background: linear-gradient(to bottom right, #2a323d, #21546d);
	background-size:100%;
	background-repeat:no-repeat;
	font: 1em/1.5 sans-serif;
	height: 100%;
	display: grid;
	place-items: center;
	
	
}


.loader-container .book,
.loader-container .book__pg-shadow,
.loader-container .book__pg {
	-webkit-animation: cover 7s ease-in-out infinite;
	        animation: cover 7s ease-in-out infinite;
}
.loader-container .book {
	
	background-color: hsl(223,90%,65%);
	
	background-color: var(--primary-l);
	border-radius: 0.25em;
	box-shadow:
		0 0.25em 0.5em hsla(0,0%,0%,0.3),
		0 0 0 0.25em hsl(223,90%,55%) inset;
	box-shadow:
		0 0.25em 0.5em hsla(0,0%,0%,0.3),
		0 0 0 0.25em var(--primary) inset;
	padding: 0.25em;
	-webkit-perspective: 37.5em;
	        perspective: 37.5em;
	position: relative;
	width: 8em;
	height: 6em;
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}
.loader-container .book__pg-shadow,
.loader-container .book__pg {
	position: absolute;
	left: 0.25em;
	width: calc(50% - 0.25em);
}
.loader-container .book__pg-shadow {
	-webkit-animation-name: shadow;
	        animation-name: shadow;
	background-image: linear-gradient(-45deg,hsla(0,0%,0%,0) 50%,hsla(0,0%,0%,0.3) 50%);
	-webkit-filter: blur(0.25em);
	        filter: blur(0.25em);
	top: calc(100% - 0.25em);
	height: 3.75em;
	-webkit-transform: scaleY(0);
	        transform: scaleY(0);
	-webkit-transform-origin: 100% 0%;
	        transform-origin: 100% 0%;
}
.loader-container .book__pg {
	-webkit-animation-name: pg1;
	        animation-name: pg1;
	background-color: hsl(223,10%,100%);
	background-color: var(--white);
	background-image: linear-gradient(90deg,hsla(223,10%,90%,0) 87.5%,hsl(223,10%,90%));
	background-image: linear-gradient(90deg,hsla(var(--hue),10%,90%,0) 87.5%,hsl(var(--hue),10%,90%));
	height: calc(100% - 0.5em);
	-webkit-transform-origin: 100% 50%;
	        transform-origin: 100% 50%;
}
.loader-container .book__pg--2,
.loader-container .book__pg--3,
.loader-container .book__pg--4 {
	background-image:
		repeating-linear-gradient(hsl(223,10%,10%) 0 0.125em,hsla(223,10%,10%,0) 0.125em 0.5em),
		linear-gradient(90deg,hsla(223,10%,90%,0) 87.5%,hsl(223,10%,90%));
	background-image:
		repeating-linear-gradient(hsl(var(--hue),10%,10%) 0 0.125em,hsla(var(--hue),10%,10%,0) 0.125em 0.5em),
		linear-gradient(90deg,hsla(var(--hue),10%,90%,0) 87.5%,hsl(var(--hue),10%,90%));
	background-repeat: no-repeat;
	background-position: center;
	background-size: 2.5em 4.125em, 100% 100%;
}
.loader-container .book__pg--2 {
	-webkit-animation-name: pg2;
	        animation-name: pg2;
}
.loader-container .book__pg--3 {
	-webkit-animation-name: pg3;
	        animation-name: pg3;
}
.loader-container .book__pg--4 {
	-webkit-animation-name: pg4;
	        animation-name: pg4;
}
.loader-container .book__pg--5 {
	-webkit-animation-name: pg5;
	        animation-name: pg5;
}

